import axios from 'axios'

let base = 'http://api.djangotest.com';

// 获取左边栏
export const getLeft = () => {
  return axios.get(`${base}/alarm_rules/`)
}

// 获取报警类型下的规则详情
export const getDetail = (id) => {
  return axios.get(`${base}/alarm_rules/${id}`)
};

// 获取用户
export const getUser = () => {
  return axios.get(`${base}/users`)
};

// 获取游戏
export const getGame = () => {
  return axios.get(`${base}/apps`)
};

// 新增规则
export const addRule = params => {
  return axios.post(`${base}/add_rule`, params)
};

// 删除规则
export const deleteRule = params => {
  return axios.post(`${base}/delete_rule`, params)
};


