<template>
  <el-main>
    <!--工具框-->
    <el-col :span="8">
    <el-form :inline="true" :model="formInline"  class="demo-form-inline">
      <!--<el-form-item>-->
        <!--<el-button type="danger" @click="handleSearch">搜索</el-button>-->
      <!--</el-form-item>-->
      <el-form-item>
        <el-button type="primary" @click="dialogFormVisible = true" icon="el-icon-plus">新建</el-button>
      </el-form-item>
      <el-form-item>
        <el-button type="danger" @click="handleMultiDelete">批量删除</el-button>
      </el-form-item>
      <el-form-item>
        <el-input v-model="formInline.game" placeholder="游戏名"></el-input>
      </el-form-item>
    </el-form>
    </el-col>
    <!--表格-->
  <el-table :data="FilteredTableData" @selection-change="handleSelectionChange">
    <el-table-column type="selection" width="55">
    </el-table-column>
    <el-table-column label="序号" width="180" prop="id" sortable v-if="false">
      <template slot-scope="scope">
        <span>{{ scope.row.id }}</span>
      </template>
    </el-table-column>
    <el-table-column label="游戏名" width="180" prop="game" sortable>
      <template slot-scope="scope">
        <span>{{ scope.row.game }}</span>
      </template>
    </el-table-column>
    <el-table-column label="收件人" width="180" prop="receiver" sortable>
      <template slot-scope="scope">
        <span>{{ scope.row.receiver }}</span>
      </template>
    </el-table-column>
    <el-table-column label="操作">
      <template slot-scope="scope">
        <el-button size="mini" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
        <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)">删除</el-button>
      </template>
    </el-table-column>
  </el-table>
    <!--弹出表单-->
    <el-dialog title="新增规则" :visible.sync="dialogFormVisible" center>
      <el-form ref="form" :model="addForm">
        <el-form-item label="邮件类型" :label-width="formLabelWidth">
          <el-input v-model="addForm.alarmType" readonly></el-input>
        </el-form-item>
        <el-form-item label="游戏名" :label-width="formLabelWidth">
          <el-select v-model="addForm.game" placeholder="请选择游戏" style="width: 100%" @focus="getGameInfo()">
            <el-option v-for="(item, index) in gameList" :key="index" :label="item.gname" :value="item.gname"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="收件人" :label-width="formLabelWidth" >
          <el-select v-model="addForm.receiver" placeholder="请选择收件人" style="width: 100%" @focus="getUserInfo">
            <el-option v-for="(item, index) in userList" :key="index" :label="item.useridentity" :value="item.useridentity"></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="onSubmit">确 定</el-button>
      </div>
    </el-dialog>

  </el-main>
</template>

<script>
  import { getDetail, getUser, getGame, addRule, deleteRule } from "../api/api";

  export default {
    name: "page1",
    data() {
      return {
        // 路由
        path: this.$router.currentRoute.path,
        // 表单数据
        tableData: [],
        // 选中项
        multipleSelection: [],
        // 删除项
        delArr: [],
        // 工具栏
        formInline: {
          game: '',
          receiver: ''
        },
        // 游戏列表
        gameList: [],
        // 用户列表
        userList: [],
        // 弹出框
        dialogFormVisible: false,
        addForm: {
          alarmType: '',
          game: '',
          receiver: '',
          // delivery: false,
          // type: [],
          // resource: '',
          // desc: ''
        },
        formLabelWidth: '120px'

      }
    },
    methods: {
      handleEdit(index, row) {
        console.log(index, row);
      },
      handleSelectionChange(val) {
        this.multipleSelection = val;
        console.log(val)
      },
      // 删除列表项
      handleDelete(index, row) {
        this.$confirm('确认删除？').then(_ => {
          // let game = row.game;
          // let receiver = row.receiver;
          let alarmId = this.id;
          let mailOperationId = row.id;
          this.delArr.push(mailOperationId);
          deleteRule({alarmId: alarmId, delArr: this.delArr}).then(res => {
            if (res.data['code'] == 1){
              this.fetchData();
            }
          })
        })
          .catch(_ => {});

      },
      // 批量删除
      handleMultiDelete( ) {
        this.$confirm('确认批量删除？').then(_ => {
          let alarmId = this.id;
          let length = this.multipleSelection.length;
          for (let i = 0; i < length; i++){
            this.delArr.push(this.multipleSelection[i].id)
          }
          deleteRule({alarmId: alarmId, delArr: this.delArr}).then(res => {
            if (res.data['code'] == 1){
              this.fetchData();
            }
          })
        })
          .catch(_ => {});
      },
      fetchData(){
        let alarmId = this.id;
        getDetail(alarmId).then(res => {
          let alarmDetail = res.data;
          let that = this;
          that.tableData = alarmDetail['mailoperation_set'];
          that.addForm['alarmType'] = alarmDetail['description'];
        })
      },
      // 获取游戏
      getGameInfo() {
        getGame().then(res => {
            let gameList = res.data;
            let that = this;
            that.gameList = gameList;
          })
      },
      // 获取用户
      getUserInfo() {
        getUser().then(res => {
            let userList = res.data;
            let that = this;
            that.userList = userList;
        })
      },

      onSubmit() {
        let formData = this.addForm;
        addRule({params: formData}).then(res => {
          // console.log(res.data)
          if (res.data['code'] == 1){
            this.fetchData();
          }
        })
        this.dialogFormVisible = false;
        // console.log('submit!');
      },
      handleSearch() {
        console.log(this.tableData)
        console.log('submit!');
      },

      handleClose(done) {
        this.$confirm('确认关闭？')
          .then(_ => {
            done();
          })
          .catch(_ => {});
      }
    },
    computed: {
      FilteredTableData() {
        let gameSearch = this.formInline.game;
        if (gameSearch) {
          return this.tableData.filter(data => {
            return Object.keys(data).some(key => {
              return String(data[key]).toLowerCase().indexOf(gameSearch) > -1
            })
          })
        }
        return this.tableData;
      }
    },
    props: ['id'],
    watch: {
      '$route': 'fetchData'
    },
    }
</script>

<style scoped>

</style>



